/* Timeline */

/*=========================*/

#tline {
  padding-bottom: 60px;
}

.tline-holder {
  margin: 0 auto;
  padding: 0px;
  padding-top: 20px;
  width: 100%;
  position: relative;
  overflow: hidden;
  list-style: none;
}

.tline-holder:before {
  /*background:#e4e4e4;*/
  content: "";
  left: 50%;
  top: 20px;
  bottom: 0px;
  position: absolute;
  width: 1px;
  margin-left: -1px;
  height: 9999px;
}

ul .tline-holder > li {
  width: 100%;
  float: left;
  display: inline-block;
}

.tline-item-content {
  width: 61.5%;
}

.tline-item-txt {
  position: relative;
  padding: 20px;
  background: #fff;
  border: 1px solid #e4e4e4;
  margin-top: -20px;
}

.tline-item-txt h3 {
  text-transform: uppercase;
  font-size: 17px;
  margin-top: 10px;
  font-weight: 700;
  color: #333;
}

.tline-item-txt .meta {
  color: #999;
}

.tline-item-left .tline-item-content:hover .date-icon,
.tline-item-right .tline-item-content:hover .date-icon {
  -webkit-animation: pulse 0.3s;
  animation: pulse 0.3s;
}

.tline-item-left {
  float: left;
  width: 100%;
  position: relative;
}

.tline-item-left .tline-item-content {
  float: left;
}

.tline-item-left .tline-item-txt {
  margin-right: 60px;
}

.tline-item-left .date-icon {
  margin-right: -20px;
  float: right;
  background-color: #303543;
  color: #fff;
}

.tline-item-right {
  width: 100%;
  float: right;
  position: relative;
}

.tline-item-right .tline-item-content {
  float: right;
}

.tline-item-right .tline-item-txt {
  margin-left: 60px;
}

.tline-item-right .date-icon {
  margin-left: -20px;
  float: left;
}

.tline-item-txt:before,
.tline-item-txt:after {
  content: " ";
  position: absolute;
  top: 24px;
  display: block;
  width: 0;
  height: 0;
  border-top: solid 15px transparent;
  border-bottom: solid 15px transparent;
}

.tline-item-left .tline-item-txt:before,
.tline-item-left .tline-item-txt:after {
  right: -18px;
  border-left: solid 20px #fff;
}

.tline-item-left .tline-item-txt:before {
  right: -20px;
  border-left-color: #e4e4e4;
}

.tline-item-right .tline-item-txt:before,
.tline-item-right .tline-item-txt:after {
  left: -18px;
  border-right: solid 20px #fff;
}

.tline-item-right .tline-item-txt:before {
  left: -20px;
  border-right-color: #e4e4e4;
}

.date-icon {
  text-align: center;
  padding-top: 12px;
  width: 40px;
  height: 40px;
  background-color: #ee4b28;
  border-radius: 100%;
  -moz-border-radius: 100%;
  -webkit-border-radius: 100%;
  color: #fff;
}

.tline-start {
